    let lyricsTop = reactive([]);   //æ¯å¥æ­è¯å¯¹åºçä½ç½®ï¼ç¬¬ä¸å¥æ­è¯ä»ä¸æ ä¸º1å¼å§
    let lyricsTopComputedTimes = ref(0);  //è¡¨ç¤ºæ­è¯ä½ç½®çè®¡ç®æ¬¡æ°ï¼åªéè¦è®¡ç®ä¸æ¬¡
    let nIndex = ref();   //å½åæ­è¯ç´¢å¼
    let touchmove = ref(false);   //è¡¨ç¤ºæ¯å¦å¤äºæ»å¨ç¶æ
    let touchMoveTime = ref()   //æ»å¨ä½ç½®æå¯¹åºçæ¶é´
    let touchMoveLyricsIndex = ref()   //æ»å¨å°çé£å¥æ­è¯çç´¢å¼
    let timer = reactive([])    //å­æ¾è®¡æ¶å¨ï¼touchendæ¶å»¶è¿åæ¶æ¾ç¤ºä½¿ç¨å°è®¡æ¶å¨ï¼å¦ææ­¤æ¶åè¿è¡æå¨ï¼å°±éè¦åæ¶è®¡æ¶å¨
    
    //è®¡ç®æ­è¯é«åº¦ï¼æ»å¨è·ç¦»ç±å½åå¥æ­è¯åä¸ä¸å¥æ­è¯çé«åº¦å³å®
    let getLyricsScrollTop = () => {
