      arr.forEach((i, index) => {
          let preP;   //åä¸å¥æ­è¯
          let nowP = arr[index]     //è·åæ¬å¥æ­è¯
          let prePHeight = 0;   //åä¸å¥æ­è¯é«åº¦ï¼é»è®¤ä¸ºç¬¬ä¸å¥æ­è¯ï¼æä»¥æ²¡æåä¸å¥æ­è¯ï¼é«åº¦ä¸º0
          let nowPHeight =  parseInt(getComputedStyle(nowP,null).height);  //è·åæ¬å¥æ­è¯é«åº¦
          let margin = parseInt(getComputedStyle(nowP,null).margin);   //æ­è¯å¤è¾¹è·ï¼é»è®¤ä¸ºç¬¬ä¸å¥æ¶åªæä¸ä¸ªå¤è¾¹è·
          if(index !== 0){    //éç¬¬ä¸å¥æ­è¯
            preP = arr[index - 1];    //è·åä¸ä¸å¥æ­è¯
            prePHeight =  parseInt(getComputedStyle(preP,null).height);  //è·åä¸ä¸å¥æ­è¯é«åº¦
            margin = margin * 2;    //è·åæ­è¯å¤è¾¹è·ï¼å¦ææåä¸å¥æ­è¯åä¸ºä¸¤åå¤è¾¹è·
          }
