    watch([() => props.currentTime, touchmove], () => {
      if(touchmove.value) return;   //å¦æå¨æ­æ¾æ¶è¿è¡æ»å¨ï¼é£ä¹æåå¯¹æ¶é´ççè§ï¼å¦ææ¯åæ­¢æ»å¨ï¼é£ä¹åå°å½åæ­è¯æå¨ä½ç½®
      //çå¬æ¶é´ï¼ä½¿æ­è¯æ»å¨
      //æ­¤å¤å¯ä»¥ä¼åï¼  å½åå·²ç»è®°å½ä¸äºå½åæ­è¯çç´¢å¼ï¼ä¸æ¬¡åªè¦å¤æ­å½åç´¢å¼çä¸ä¸å¥å³å¯ï¼ä¸ç¨å¨é¨éå
      lyricsTimes.forEach((i, index) => {
