          // if(nIndex.value === index) return;    //å¦æè¯¥å¥æ­è¯åä¸ä¸å¥æ­è¯ä¸æ ·åè¿åï¼ä½æ¯ä¼äº§çå¦ææ¯æåæåµä¸ï¼nIndexå§ç»ç­äºindexä»èæ æ³åå°åä½ç½®
          a.value.scrollTop = lyricsTop[index + 1];   //lyricsTopç¬¬ä¸é¡¹ä¸ºåå§ä½ç½®ï¼ä»1å¼å§ææ¯æ­è¯å¯¹åºçä½ç½®ï¼æä»¥è¦å 1
          nIndex.value = index    //æ´æ°å½åæ­è¯ç´¢å¼
        }
