    onMounted(() => {
      //æ­è¯æå¨
      a.value.addEventListener('touchmove', () => {   //æ­¤å¤ä¸è½ä½¿ç¨èæµèçæ§è½ï¼å ä¸ºå¦æç¨æ·æåæ¯å¿«éæ¾å¼è§¦åäºæ¯æ§æ»å¨çè¯ï¼
                                                            //ç±äºtouchmoveäºä»¶å¨æåè§¦åï¼åå ä¸ºèæµäº§çå»¶è¿ï¼æä»¥ä¼åºç°æææ¾å¼touchendäºä»¶åçååè§¦åtouchmoveäºä»¶ï¼æ¸é¤å®æ¶å¨ä»èåºç°é®é¢
                                                            //èå¦ææ¯æåæ²¡æè§¦åæ¯æ§æ»å¨ï¼å ä¸ºææå¨ç¦»å¼å±å¹åä¸æ®µæ¶é´åå°±å·²ç»åæ­¢äºtouchmoveï¼æä»¥ä¸ä¼äº§çtouchmoveå»¶æ¶è§¦åçç°è±¡
                                                            //è¯´å°åºæ¯æææåè§¦åtouchmoveåtouchendçæ¶é´é´éæ¯å¦å¤§äºäºèæµçæ¶é´ï¼è¥å°äºå°±ä¼åºç°è¯¥é®é¢
        //å¦ææå¨æ¶å­å¨æªæ§è¡çå®æ¶å¨ï¼é£ä¹æ¸é¤ä»ä»¬
        if(timer.length !== 0){
          timer.forEach(i => {
            clearTimeout(i)
          })
