}
/* 
æ­è¯æ»å¨ç»ä»¶
  åè½ï¼
    1ãæ­è¯æ»å¨ï¼
      1ãçå¬audioåç´ ä¸çtimeupdateäºä»¶ï¼å®æ¶è·åå½åæ¶é´
      2ãv-foréåæ­è¯ï¼å¸¦æå¯¹åºæ¶é´ï¼ï¼æ­è¯é¦æ¬¡onUpdatedçæ¶åè®¡ç®åºæ¯å¥æ­è¯å¯¹åºçscrollTop(è¯¥å±æ§å¨touchmoveäºä»¶ä¸ä¸ä¼å¤±æï¼ètopå±æ§ä¼å¤±æ)æ»å¨è·ç¦»ï¼æ¾å¨æ°ç»ä¸­
      3ãwatchçå¬å½åæ¶é´å¹¶ä¸æ¯å¥æ­è¯å¯¹åºçæ¶é´è¿è¡å¯¹æ¯ï¼ä½¿ç¨JSæä½DOMï¼scrollTopå±æ§ï¼è¿è¡æ»å¨å°å¯¹åºæ­è¯çä½ç½®
    2ãæ»å±æ¶åæ­¢æ­è¯èªå¨æ»å¨å¹¶æ¾ç¤ºæ­æ¾æé®ãæ¨ªçº¿åå½åæ»å¨å°çæ­è¯å¯¹åºçæ¶é´
      1ãtouchmoveäºä»¶è§¦åæ¶watchçå¬æ¶é´è¿è¡æ­è¯æ»å¨çè¡ä¸ºæå
      2ãå¨touchmoveäºä»¶éæ§å¶æ¨ªçº¿çæ¾ç¤º
      3ãæ ¹æ®æ¨ªçº¿åæ¯å¥æ­è¯çç¸å¯¹ä½ç½®ï¼æ­¤å¤è·åæ¨ªçº¿åæ¯å¥æ­è¯è·ç¦»å±å¹çyè·ç¦»ï¼ï¼
        å¦ææå¥æ­è¯çyå°äºæ¨ªçº¿çyå¹¶ä¸ä¸ä¸å¥æ­è¯çyå¤§äºæ¨ªçº¿çyï¼é£ä¹å°±è·åè¯¥å¥æ­è¯å¯¹åºçæ¶é´æ¾ç¤ºåºæ¥
      4ãtouchendæ¶å»¶è¿åæ¶æ¾ç¤ºï¼å¹¶å°è¯¥è®¡æ¶å¨idä¿å­ä¸æ¥ï¼ä¸æ¬¡è¿å¥touchmoveæ¶åæ¶æªæ§è¡çè®¡æ¶å¨
    3ãæ»å±ç»ææ¶ç¹å»æ­æ¾æé®è·³è½¬å°ææ»å°çä½ç½®
      æ¹åæ¶é´å³å¯ï¼watchä¼çå¬å°æ¶é´ååè¿è¡èªå¨è·³è½¬ï¼
    4ãæ»å±ç»æä¸ç¹å»æ­æ¾æé®1såèªå¨åå°æ­æ¾ä½ç½®
      çå¬touchmoveå±æ§ï¼å½å®åçååä¸å½åç¶æä¸ºfalseæ¶å°æ­è¯æ»å¨å°å¯¹åºä½ç½®
  bugsï¼    
    æ­è¯è¶åºé¿åº¦æè¡åå¯¼è´æ­è¯é«åº¦ä¸åï¼å¦ä½è®¡ç®é«åº¦ï¼
      è·åå°å½åå¥æ­è¯åä¸ä¸å¥æ­è¯çDOMåç´ ï¼è·åä»ä»¬çé«åº¦åæä½DOMè¿è¡æ»å¨ï¼ä¸¤ä¸ªDOMåç´ é«åº¦çä¸åä¹ååå å¤è¾¹è·ï¼
    touchmoveå¯¼è´topå®ä½å¤±å
      ä½¿ç¨scrollTopä»£æ¿top
    å¦ææåç¨æ·ææå¿«éç¦»å¼å±å¹åºç°æ¯æ§æ»å¨æ¶ï¼ä¼åºç°ç±äºå¯¹touchmoveäºä»¶è¿è¡äºèæµå¯¼è´å¶å¨touchendäºä»¶åå»¶è¿è§¦åï¼å¯¼è´å®æ¶å¨è¢«æ¸é¤
      åæ¶èæµ
    ç»´æ¤å½åé«äº®æ­è¯nIndexåindexï¼å¤æ­å¶æ¯å¦ç¸ç­ï¼è¥ç¸ç­åæ éæä½DOMç¨ä»¥æé«æ§è½ï¼ä½æ¯ä¼åºç°æ»å¨ç»æåéè¦åä½æ¶ï¼è¥æ­è¯è¿æ¯åçå¨åå¥ï¼åæ æ³åä½
      åæ¶è¯¥è¡ä¸º
    é²æ­¢æ¯æ§æ»å¨æ¶å°±åä½
      å°touchendå®æ¶å¨çå»¶è¿è®¾çé¿ä¸äº
    æåååºç°çé®é¢ï¼
      è§CSDNæç« 
    æåä¼åï¼
      å°ä¸æ¹åºä½¿ç¨externalä¸æåå°æåæä»¶ä¸­ï¼åå°æååçä½ç§¯

    
*/ 
</script>

